import "./Button.scss";

const STYLES = [
    "btn--primary",
    "btn--secondary",

];

const SIZE = ["btn--large", "btn--medium"];

export const Button = ({
    children,
    type,
    onClick,
    buttonStyle,
    buttonSize
}) => {

    const checkButtonStyle = STYLES.includes(buttonStyle) 
    ? buttonStyle 
    : STYLES[0];

    const checkButtonSize = SIZE.includes(buttonSize)
    ? buttonSize
    : SIZE[0];


    return(
        <button className={`btn btn--animated ${checkButtonStyle} ${checkButtonSize}`} onClick={onClick} type={type}>
            {children}
        </button>
    )

};