import "./Contact.scss";
import contactImg from "../../img/contact-img.svg";
import profileImg from "../../img/avatar-profile.svg";
import { Button } from "../../components/Button";
import {CopyToClipboard} from 'react-copy-to-clipboard';
import React, { useState , useRef} from 'react';

const Contact = () => {
    const [isCopied, setIsCopied] = useState(false);

    const messageContainer = useRef();

    const handleCopy = () => {
        setIsCopied(true);
    };

    const copyButton = () => {
        if (isCopied === true){
            messageContainer.current.classList.remove("message-inactive");
            messageContainer.current.classList.add("message-animation");
            setTimeout(() => {
                messageContainer.current.classList.add("message-inactive");
                
                
            }, 4000);
        }
    }


    return(
        <main className="container-contact">

        <section className="hero-contact">
            <article className="hero-contact__container">
            <div className="hero-contact__text">
                <h1 className="heading-primary move-in-heading">Contact me</h1>
                <span className="heading-tertiary move-in-subtitle">Please, feel free to contact me.</span>
            </div>
            <img className="hero-contact__img img-hero-animation" src={contactImg} loading="lazy" alt="sebgraph illustration"/>
        </article>
        </section>

        <section className="contact-btns">
            <article className="contact-btns__text-box">
            <img className="profile__img" src={profileImg} alt="avatar sebgraph"/>
            <h2 className="heading-secondary">Let’s talk</h2>
            <p className="paragraph">Please, feel free to contact me and let me be part of your great project.</p>
            </article>
            <div className="contact-btns__btns">
                <a href="mailto:sebgraph7@gmail.com" className="email">
                <Button 
                type="button" 
                buttonStyle="btn--secondary" 
                buttonSize="btn--large">
                    Email me
                </Button>
                </a>
                
{/*                 <button id="myBtn" data-clipboard-text="sebgraph7&commat;gmail&period;com"  className="tippy-box copy-button btn-contact btn-contact--primary btn-contact--animated">
                    Copy mail
                </button> */}

                <CopyToClipboard text="sebgraph7@gmail.com" onCopy={handleCopy}>
                <Button
                onClick={copyButton} 
                type="button" 
                buttonStyle="btn--primary" 
                buttonSize="btn--large">
                    <div className="message-inactive messageContainer" ref={messageContainer}>
                    <h1 className="message">Copied</h1>
                    </div>
                    Copy mail
                </Button>
            
                </CopyToClipboard>
                
            </div>



        </section>



        

    </main>
    );
};

export default Contact;